@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');


* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html{
  background: linear-gradient(45deg, black, #085e4669);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 100%;
}

.kain-logo{
  z-index: 9999;
  display: flex;
  font-size: 12px;
  float: left;
  margin: 33px 80px;
}

.kain-logo h1{
  font-size: 16px;
  font-weight: 600;
  color: white;
  font-family: 'Poppins';
}

.kain-logo a{
  text-decoration: none;
  font-family: 'Orbitron';
  color: #47d547;
  text-shadow: 0 0 2px white;
  font-size: 18px;
  margin-left: 10px;
}

@media all {

	div#pokedex{
		width: 750px;
		margin: 50px auto 0 auto;
	}

	div#left{
		width: 400px;
		height: 500px;
		float: left;
		position: relative;
		z-index: 1;
	}

	div#right{
		width: 350px;
		height: 500px;
		float: left;
		position: relative;
	}

	div#curve1_left{
		width: 201px;
		height: 85px;
		background-color: #8b0000;
		float: left;
		padding: 15px 0 0 15px;

		position: absolute;
		top: 0;
		left: 0;

		box-shadow: -10px 9px #5e0000;
		-webkit-box-shadow: -10px 9px #5e0000;
		-moz-box-shadow: -10px 9px #5e0000;
		-o-box-shadow: -10px 9px #5e0000;

		border-bottom-right-radius: 85px 60px;
		-webkit-border-bottom-right-radius: 85px 60px;
		-moz-border-bottom-right-radius: 85px 60px;
		-o-border-bottom-right-radius: 85px 60px;

		border-top-left-radius: 30px;
		-webkit-border-top-left-radius: 30px;
		-moz-border-top-left-radius: 30px;
		-o-border-top-left-radius: 30px;
	}


	div#bg_curve1_left{
		width: 400px;
		height: 80px;
		background-color: #8b0000;

		border-top-left-radius: 30px;
		-webkit-border-top-left-radius: 30px;
		-moz-border-top-left-radius: 30px;
		-o-border-top-left-radius: 30px;
	}

	div#curve2_left{
		width: 216px;
		height: 451px;
		background-color: #c00d0d;
		float: right;

		position: absolute;
		bottom: 0;
		right: 0;

		border-top-left-radius:85px 60px;
		-webkit-border-top-left-radius: 85px 60px;
		-moz-border-top-left-radius: 85px 60px;
		-o-border-top-left-radius: 85px 60px;
	}

	div#bg_curve2_left{
		width: 400px;
		height: 420px;
		background-color: #c00d0d;

		box-shadow: -10px 9px #5e0000;
		-webkit-box-shadow: -10px 9px #5e0000;
		-moz-box-shadow: -10px 9px #5e0000;
		-o-box-shadow: -10px 9px #5e0000;

		border-bottom-left-radius: 30px;
		-webkit-border-bottom-left-radius: 30px;
		-moz-border-bottom-left-radius: 30px;
		-o-border-bottom-left-radius: 30px;
	}

	div#buttonGlass{
		width: 60px;
		height: 60px;
		margin-right: 15px;
		border: 5px solid #fff;
		float: left;

		box-shadow: 0 0 10px #490000;
		-webkit-box-shadow: 0 0 10px #490000;
		-moz-box-shadow: 0 0 10px #490000;
		-o-box-shadow: 0 0 10px #490000;
		
		background: radial-gradient(#05fbfb, #29abe3);
		background: -webkit-radial-gradient(#05fbfb, #29abe3);
		background: -moz-radial-gradient(#05fbfb, #29abe3);
		background: -o-radial-gradient(#05fbfb, #29abe3);
		
		border-radius: 35px;
		-webkit-border-radius: 35px;
		-moz-border-radius: 35px;
	   	-o-border-radius: 35px;
	}

	div#reflect{
		width: 30px;
		height: 18px;
		margin: 3px 0 0 15px;
		background: #fff;
		opacity: 0.6;
		border-radius: 15px 9px;
		-webkit-border-radius: 15px 9px;
		-moz-border-radius: 15px 9px;
	   	-o-border-radius: 15px 9px; 
	}

	div#miniButtonGlass1{
		width: 20px;
		height: 20px;
		margin-right: 10px;
		float: left;

		box-shadow: 0 0 10px #490000;
		-webkit-box-shadow: 0 0 10px #490000;
		-moz-box-shadow: 0 0 10px #490000;
		-o-box-shadow: 0 0 10px #490000;

		border-radius: 20px;
		-webkit-border-radius: 20px;
		-moz-border-radius: 20px;
		-o-border-radius: 20px;
		
		background: radial-gradient(#fb7b7b, #fb0505);
		background: -webkit-radial-gradient(#fb7b7b, #fb0505);
		background: -moz-radial-gradient(#fb7b7b, #fb0505);
		background: -o-radial-gradient(#fb7b7b, #fb0505);
	}

	div#miniButtonGlass2{
		width: 20px;
		height: 20px;
		margin-right: 10px;
		float: left;

		box-shadow: 0 0 10px #490000;
		-webkit-box-shadow: 0 0 10px #490000;
		-moz-box-shadow: 0 0 10px #490000;
		-o-box-shadow: 0 0 10px #490000;

		border-radius: 20px;
		-webkit-border-radius: 20px;
		-moz-border-radius: 20px;
		-o-border-radius: 20px;
		
		background: radial-gradient(#fbfb9b, #fbfb05);
		background: -webkit-radial-gradient(#fbfb9b, #fbfb05);
		background: -moz-radial-gradient(#fbfb9b, #fbfb05);
		background: -o-radial-gradient(#fbfb9b, #fbfb05);
	}

	div#miniButtonGlass3{
		width: 20px;
		height: 20px;
		margin-right: 10px;
		float: left;

		box-shadow: 0 0 10px #490000;
		-webkit-box-shadow: 0 0 10px #490000;
		-moz-box-shadow: 0 0 10px #490000;
		-o-box-shadow: 0 0 10px #490000;

		border-radius: 20px;
		-webkit-border-radius: 20px;
		-moz-border-radius: 20px;
		-o-border-radius: 20px;
		
		background: radial-gradient(#b0fb7b, #50fb05);
		background: -webkit-radial-gradient(#b0fb7b, #50fb05);
		background: -moz-radial-gradient(#b0fb7b, #50fb05);
		background: -o-radial-gradient(#b0fb7b, #50fb05);
	}

	div#junction{
		width: 60px;
		height: 451px;
		float: right;
		
		background: rgb(0 0 0 / 23%);
	}

	div#junction1{
    height: 85px;
    background-color: #460404;
    opacity: 0.3;
    border-radius: 6px;
    box-shadow: 0 0 14px black inset;
    margin-top: 0px;
    margin-bottom: 282px;
    border-top: 2px solid #330000;
    border-bottom: 2px solid #330000;
	}

	div#junction2{
    height: 85px;
    background-color: #460404;
    opacity: 0.4;
    border-radius: 6px;
    box-shadow: 0 0 27px black inset;
    border-top: 2px solid #330000;
    border-bottom: 2px solid #330000;
  }
	

	div#screen{
		height: 393px;
		width: 325px;
		padding: 0 20px;
		background-color: #b0b0b0;
		float: left;
		position: absolute;
		top: 97px;
		left: 7px;
		border-radius: 15px;
	}

	div#screen:after{
		content: "";
		border-top: 40px solid #b0b0b0;
		border-left: 40px solid #c00d0d;
		height: 0;

		position: absolute;
		bottom: 0;
		left: 0;
	}

	div#picture{       
		height: 332px;
		width: 286px;
		margin-top: 21px;
		margin-bottom: 9px;
    background: linear-gradient(45deg, #ecedf5, #ffffff, #ecedf5, #ffffff, #ecedf5, #ffffff, #ecedf5, #ffffff, #ecedf5, #ffffff, #ecedf5);
		border: 3px solid #494949;
		clear: both;
		border-radius: 15px;
	}

	div#picture img{
    display: block;
    margin: 15px auto;
    filter: drop-shadow(5px 4px 6px black);
    height: 210px;
    width: 250px;
	}

	div#topPicture{
		margin: 6px auto;
		width: 40px;
	}

  .info-pokemon{
    padding: 5px 15px;
    font-family: 'Courier New', Courier, monospace;
  }

  .info-pokemon h2{
    font-weight: 900;
    font-size: 19px;
    font-family: "Courier New", Courier, monospace;
    letter-spacing: 4px;
    text-transform: capitalize;
  }

  .info-pokemon p{
    font-size: 14px;
    font-family: "Courier New", Courier, monospace;
    font-weight: 900;
    letter-spacing: 4px;
    text-shadow: 0 0 14px black;
  }

  .choice-pokemon{
    font-size: 16px;
    letter-spacing: 0px!important;
  }

  .blinking-cursor:after{
    content: '|';
    color: black;
    animation: blink 1s step-start infinite;
  }

  @keyframes blink {
    50%{
      opacity: 0;
    }    
  }

	div#buttontopPicture1, div#buttontopPicture2{
		height: 8px;
		width: 8px;
		background-color: #c00d0d;
		border: 1px solid #000;
		float: left;

		border-radius: 15px;
		-webkit-border-radius: 15px;
		-moz-border-radius: 15px;
		-o-border-radius: 15px;
	}

	div#buttontopPicture1{
		margin-right: 20px;
	}

	div#buttonbottomPicture{
		height: 26px;
		width: 26px;
		background-color: #c00d0d;
		margin-left: 35px;
		float: left;

		border-radius: 30px;
		-webkit-border-radius: 30px;
		-moz-border-radius: 30px;
		-o-border-radius: 30px;

		box-shadow: -2px 1px #5e0000;
		-webkit-box-shadow: -2px 1px #5e0000;
		-moz-box-shadow: -2px 1px #5e0000;
		-o-box-shadow: -2px 1px #5e0000;

		background: linear-gradient(top, #c00d0d 0%, #8b0000 80%);	
		background: -webkit-linear-gradient(top, #c00d0d 0%, #8b0000 80%);
		background: -moz-linear-gradient(top, #c00d0d 0%, #8b0000 80%);
		background: -o-linear-gradient(top, #c00d0d 0%, #8b0000 80%);
	}

	div#speakers{
		float: right;
		width: 70px;
		height: 25px;
		margin-right: 0px;
	}

	div.sp{
	    height: 2px;
        margin-bottom: 2px;
		background-color: #494949;

		border-radius: 30px;
		-webkit-border-radius: 30px;
		-moz-border-radius: 30px;
		-o-border-radius: 30px;
	}

	div#leftT{
		width: 0;
		height: 0;
		border-top: 10px solid transparent;
		border-right: 10px solid #111;
		border-bottom: 10px solid transparent;

		position: absolute;
		top: 5px;
		left: 4px;
	}

	div#rightT{
		width: 0;
		height: 0;
		border-top: 10px solid transparent;
		border-left: 10px solid #111;
		border-bottom: 10px solid transparent;

		position: absolute;
		top: 5px;
		right: 5px;
	}

	div#midCircle{
		width: 20px;
		height: 20px;

		position: absolute;
		top: 5px;
		left: 4px;

		border-radius: 30px;
		-webkit-border-radius: 30px;
		-moz-border-radius: 30px;
		-o-border-radius: 30px;

		background: radial-gradient(#111, #222);
		background: -webkit-radial-gradient(#111, #222);
		background: -moz-radial-gradient(#111, #222);
		background: -o-radial-gradient(#111, #222);
	} 

	div#curve1_right{
		width: 166px;
		height: 451px;
		background-color: #c00d0d;
		float: right;

		position: absolute;
		bottom: 0;
		left: 0;

		border-top-right-radius:85px 60px;
		-webkit-border-right-left-radius: 85px 60px;
		-moz-border-right-left-radius: 85px 60px;
		-o-border-right-left-radius: 85px 60px;
	}


	div#bg_curve1_right{
		width: 350px;
		height: 80px;
		border-top-right-radius: 30px;
	}

	div#curve2_right{
		width: 216px;
		height: 100px;
		float: left;
		position: absolute;
		top: 0;
		right: 0;
		border-bottom-left-radius: 85px 60px;
	}

	div#bg_curve2_right{
		width: 390px;
		height: 420px;
		background-color: #c00d0d;

		border-bottom-right-radius: 30px;
		-webkit-border-bottom-right-radius: 30px;
		-moz-border-bottom-right-radius: 30px;
		-o-border-bottom-right-radius: 30px;

		box-shadow: -10px 9px #5e0000;
		-webkit-box-shadow: -10px 9px #5e0000;
		-moz-box-shadow: -10px 9px #5e0000;
		-o-box-shadow: -10px 9px #5e0000;
	}

	div#stats{
    height: 340px;
    width: 380px;
    padding: 5px;
    background-color: #032D28;
    z-index: 1;
    font-size: 13px;
    font-family: arial, sans-serif;
    position: absolute;
    top: 90px;
    left: 5px;
    border-radius: 15px;
    box-shadow: 0 0 20px #740606 inset;
	}

	#stats .list-pokemons{
		width: 100%;
		height: 100%;
	}

  #stats .list-pokemons h4{
    color: white;
  }

	#stats .list-pokemons ul{
    display: flex;
    flex-wrap: wrap;
    font-size: 13px;
    font-family: "Courier New", Courier, monospace;
    justify-content: center;
    align-items: center;
    height: 100%;
    }

  #stats .list-pokemons li{
    color: #ffffff;
    border: 0;
    list-style: none;
    padding: 5px 0;
    border-radius: 4px;
    transition: 0.2s;
    cursor: pointer;
    font-weight: 800;
    margin: 0 5px;
    text-align: center;
    width: 45%;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    box-shadow: 0px 0px 0 1px #565352cc;
  }

	.list-pokemons li::first-letter{
		text-transform: uppercase;
	}

  .list-pokemons li:hover {
    background-color: #ffffff7a;
  }

  .list-pokemons li:active {
    box-shadow: 0 0 0 0 #7c716b, inset 4px 4px 4px #7c716b;
    transform: translate(4px, 4px);
}

	div#yellowBox1{
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    background-color: #efefec;
    position: absolute;
    top: 450px;
    left: 55px;
    border-radius: 5px;
    border: 1px solid black;
    box-shadow: 0 0 10px #000000 inset;
    cursor: pointer;
	}

	div#yellowBox2{
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    position: absolute;
    top: 450px;
    left: 220px;
    border-radius: 5px;
    border: 1px solid black;
    box-shadow: 0 0 10px #000000 inset;
    cursor: pointer;
	}

  div#yellowBox1:hover,
  div#yellowBox2:hover{
      background-color: rgb(155, 145, 145);
  }

  div#yellowBox1 button{
    text-shadow: 0 0 1px black;
    font-size: 18px;
    font-weight: 900;
    font-family: "Courier New", Courier, monospace;
    padding: 5px 15px;
    background-color: #efefec;
    border-radius: 5px;
    box-shadow: 0 0 10px #000000 inset;
    cursor: pointer;
    transition: 0.4s;
  }

  div#yellowBox2 button{
    text-shadow: 0 0 1px black;
    font-size: 18px;
    font-weight: 900;
    font-family: 'Courier New', Courier, monospace;
    padding: 5px 35px;
    background-color: #efefec;
    border-radius: 5px;
    box-shadow: 0 0 10px #000000 inset;
    cursor: pointer;
    transition: 0.4s;
  }

}